.center,
.links {
  color: #eeeeee;
}
.center,
.links,
.footer {
  width: fit-content;
  margin: 0 auto;
}

.center {
  font-size: 2.1vw;
  margin-top: 36vh;
}
.links {
  margin-top: 4vh;
  font-size: 1.4vw;
  user-select: none;
  display: flex;
}
.link,
.bad-link {
  position: relative;
  margin: 0 3vw;
  color: #eeeeee;
  text-decoration: none;
  &::before {
    content: "<";
    position: absolute;
    right: calc(100% + 0.4vw);
    transition-duration: 200ms;
  }
  &::after {
    content: ">";
    position: absolute;
    left: calc(100% + 0.4vw);
    transition-duration: 200ms;
  }
}
.link {
  &:visited {
    color: #ffec9e;
  }
  &:hover {
    cursor: pointer;
    color: gold;
    &::before {
      transform: translateX(-0.2vw);
    }
    &::after {
      transform: translateX(0.2vw);
    }
  }
}
.sorry {
  position: absolute;
  font-size: 1.2vw;
  top: 100%;
  color: #d03e24;
}
.footer {
  font-size: 1vw;
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: 10vh;
  user-select: none;
}

@media (max-width: 800px) {
  .center {
    font-size: 4.2vw;
  }
  .links {
    font-size: 2.8vw;
  }
  .footer {
    font-size: 2vw;
  }
}
